import Card from '@mui/material/Card';
import FormControl from '@mui/material/FormControl';
import React, { useMemo, useState } from 'react';
import { Button, CardContent, InputLabel, MenuItem, Select, Typography } from '@mui/material';
import { TextArea } from './components/TextArea/TextArea';
import { DatePicker, LocalizationProvider, TimePicker } from '@mui/x-date-pickers';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';
import dayjs, { Dayjs } from 'dayjs';
import ReactConfetti from 'react-confetti';

import './App.css';


interface FormValue {
    tower: string;
    towerFloor: string;
    meetingRoom: string;
    comment: string;
    date: Dayjs | null;
    startTime: Dayjs | null;
    endTime: Dayjs | null;
}

const defaultValue: FormValue = {
    tower: '',
    towerFloor: '',
    meetingRoom: '',
    date: null,
    startTime: null,
    endTime: null,
    comment: '',
};

function App() {

    const [formValue, setFormValue] = useState<FormValue>(defaultValue);

    /**
     *  MUI x-date-pickers     .      
     *        - 
     *  https://www.npmjs.com/package/react-final-form
     *
     *      "    "
     *       )
     */
    const [date, setDate] = React.useState<Dayjs | null>(dayjs(null));
    const [startTime, setStartTime] = React.useState<Dayjs | null>(dayjs(null));
    const [endTime, setEndTime] = React.useState<Dayjs | null>(null);

    /**
     *      onChange       ,
     *      .
     *  useCallback   ,        ,     
     *    ,           useCallback,
     *           
     */
    const updateFormField = <T extends keyof FormValue>(
        field: T,
        value: FormValue[T],
    ) => {
        setFormValue(prev => ({
            ...prev,
            [field]: value,
        }));
    }

    const towers = useMemo(() => [' ', ' '], []);

    const floors = useMemo(() => {
        return Array.from({ length: 25 }, (_, i) => (i + 3).toString());
    }, [])

    const meetingRooms = useMemo(() => {
        return Array.from({ length: 10 }, (_, i) => ({
            number: (i + 1).toString(),
            name: ` ${i + 1}`,
        }));
    }, [])

    const [confetti, setConfetti] = useState(false);

    const submitForm = () => {
        setConfetti(prev => !prev);

        console.log({
            ...formValue,
            date: date?.toJSON(),
            startTime: `${startTime?.hour()}:${startTime?.minute()}`,
            endTime: `${endTime?.hour()}:${endTime?.minute()}`,
        });
    }

    const clearForm = () => {
        setFormValue(defaultValue);
        setDate(null);
        setStartTime(null);
        setEndTime(null);
    }

    return (
        <>
            { confetti && <ReactConfetti /> }

            <Card
                variant="outlined"
                style={{ width: '600px' }}
            >
                <CardContent>
                    <Typography variant="h5" component="div" className="form-header">
                          !
                    </Typography>
                    <div className="controls-wrapper">
                        <FormControl fullWidth>
                            <InputLabel id="tower-label"> </InputLabel>
                            <Select
                                labelId="tower-label"
                                label=" "
                                value={formValue.tower}
                                onChange={e => updateFormField('tower', e.target.value)}
                            >
                                {
                                    towers.map(tower => (
                                        <MenuItem
                                            key={tower}
                                            value={tower}
                                        >{tower}</MenuItem>
                                    ))
                                }
                            </Select>
                        </FormControl>

                        <FormControl fullWidth>
                            <InputLabel id="tower-floor-label"> </InputLabel>
                            <Select
                                labelId="tower-floor-label"
                                label=" "
                                onChange={e => updateFormField('towerFloor', e.target.value)}
                                value={formValue.towerFloor}
                                name="towerFloor"
                            >
                                {
                                    floors.map(floorNumber => (
                                        <MenuItem
                                            key={floorNumber}
                                            value={floorNumber}
                                        >{floorNumber}</MenuItem>
                                    ))
                                }
                            </Select>
                        </FormControl>

                        <FormControl fullWidth>
                            <InputLabel id="meeting-room-label"> </InputLabel>
                            <Select
                                labelId="meeting-room-label"
                                label=" "
                                onChange={e => updateFormField('meetingRoom', e.target.value)}
                                value={formValue.meetingRoom}
                            >
                                {
                                    meetingRooms.map(room => (
                                        <MenuItem
                                            key={room.number}
                                            value={room.number}
                                        >{room.name}</MenuItem>
                                    ))
                                }
                            </Select>
                        </FormControl>


                        <LocalizationProvider
                            dateAdapter={AdapterDayjs}
                        >
                            <DatePicker
                                label=" "
                                value={date}
                                onChange={setDate}
                            />

                            <div className="time-container">
                                <TimePicker
                                    ampm={false}
                                    label=" "
                                    value={startTime}
                                    onChange={setStartTime}
                                />

                                <TimePicker
                                    ampm={false}
                                    label=" "
                                    value={endTime}
                                    onChange={setEndTime}
                                />
                            </div>
                        </LocalizationProvider>


                        <TextArea
                            multiline
                            placeholder=" "
                            className="comment"
                            value={formValue.comment}
                            onChange={e => updateFormField('comment', e.target.value)}
                        />
                    </div>

                    <div className="actions-wrapper">
                        <Button
                            variant="outlined"
                            color="error"
                            onClick={clearForm}
                        >
                            
                        </Button>

                        <Button
                            variant="contained"
                            color="success"
                            onClick={submitForm}
                        >
                            
                        </Button>
                    </div>
                </CardContent>
            </Card>
        </>
    );
}

export default App;
